.App {
  text-align: center;
}

body
{
  background-color: #282c34;
}
.App-logocontainer
{
  display: flex;
  align-items: center;
  justify-content: center;
  background: #cfbb00;
}

.App-logo {
  height: 30vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-container {
  background-color: #282c34;
  min-height: 95vh;
  display: flex;
  flex-direction:column;
  align-items:flex-start ;
  font-size: calc(10px + 2vmin);
  color: white;
  border:#cfbb00 1px solid;
}

.App-header {
  background-color: #282c34;
  display: flex;
  flex-direction: row;
  font-size: calc(10px + 2vmin);
  color: white;
  border:#cfbb00 1px solid;
}

.App-header-infotab
{
font-size: calc(10px + .7vmin);
  margin-left: 2vw;
  border:#cfbb00 1px solid;
  text-align: left;
  padding: 1vw;
}

.App-section {
  border: #cfbb00 1px solid;
  min-height: 62vh;
  min-width: 95vw;
}

.App-link {
  color: #cfbb00;
}

